import React, { Component } from "react";
import "./App.css";
import "./Bulma-0.9.1.min.css";
import localforage from "localforage";

class App extends Component {
  constructor() {
    super();
    this.state = {
      items: [],
      description: "",
    };
    this.handleSearchButtonSubmit = this.handleSearchButtonSubmit.bind(this);
    this.input = React.createRef();
  }

  componentDidMount() {
    this.refreshList();
  }

  refreshList = () => {
    let entries = [];

    // javascript react promises are awkward and weird
    var that = this;

    localforage
      .iterate(function (value, key, iterationNumber) {
        // Resulting key/value pair -- this callback
        // will be executed for every item in the
        // database.
        //console.log([key, JSON.stringify(value)], iterationNumber);
        //add it to the array
        entries.push(value);
      })
      .then(function () {
        console.log("Iteration has completed");
        //console.log('Sorting list with date descending and redisplaying...');
        // sort by date descending
        let sortedEntries = entries.sort((a, b) => {
          let aTimestamp = new Date(a["timestamp"]).getTime();
          let bTimestamp = new Date(b["timestamp"]).getTime();
          return bTimestamp - aTimestamp;
        });
        //console.log(`sortedEntries = ${JSON.stringify(sortedEntries)}`);
        that.setState({ items: sortedEntries });
      })
      .catch(function (err) {
        // This code runs if there were any errors
        console.log(err);
      });
  };

  handleFormChange(attr, event) {
    console.log(attr, event.target.value);
    this.setState({
      [attr]: event.target.value,
    });
  }

  handleSearchButtonSubmit(event) {
    let entries = [];

    // javascript react promises are awkward and weird
    var that = this;

    let inputVal = this.input.current.value;
    console.log(`inputVal = ${inputVal}`);

    localforage
      .iterate(function (value, key, iterationNumber) {
        // Resulting key/value pair -- this callback
        // will be executed for every item in the
        // database.
        //console.log([key, JSON.stringify(value)], iterationNumber);
        let stringifiedAndLowerCasedItem = JSON.stringify(value).toLowerCase();
        //console.log(stringifiedAndLowerCasedItem);
        if (stringifiedAndLowerCasedItem.indexOf(inputVal.toLowerCase()) > -1) {
          //add it to the array
          entries.push(value);
        }
      })
      .then(function () {
        console.log("Iteration has completed");
        //console.log('Sorting list with date descending and redisplaying...');
        // sort by date descending
        let sortedEntries = entries.sort((a, b) => {
          let aTimestamp = new Date(a["timestamp"]).getTime();
          let bTimestamp = new Date(b["timestamp"]).getTime();
          return bTimestamp - aTimestamp;
        });
        //console.log(`sortedEntries = ${JSON.stringify(sortedEntries)}`);
        that.setState({ items: sortedEntries });
        that.setState({ filterText: inputVal });
      })
      .catch(function (err) {
        // This code runs if there were any errors
        console.log(err);
      });
  }

  handleListFilterChange = (event) => {
    this.refreshList();

    let inputVal = event.target.value;
    console.log(`inputVal = ${inputVal}`);
    const filteredArray = this.state.items.filter((item) => {
      let stringifiedAndLowerCasedItem = JSON.stringify(item).toLowerCase();
      console.log(stringifiedAndLowerCasedItem);
      if (stringifiedAndLowerCasedItem.indexOf(inputVal.toLowerCase()) > -1) {
        return item;
      }
    });

    if (filteredArray.length > 0) {
      var that = this;
      setTimeout(function () {
        that.setState({ items: filteredArray });
        that.setState({ filterText: inputVal });
      }, 1000);
    }
  };

  handleItemClick = (item) => {
    //console.log(`Yay! you clicked an item! ${JSON.stringify(item)}`);
    this.showItemDetails(item);
    currentItem = item;
  };

  handleDeleteItemClick = (event) => {
    event.preventDefault();
    removeFromLocalDB(currentItem);
    this.refreshList();
  };

  handleFormSubmit = (event) => {
    event.preventDefault();
    let today = new Date();

    // Get input
    let entry = {
      timestamp: today.toISOString(),
      description: this.state.description,
    };

    // save entry to db
    addToLocalDB(today.getTime().toString(), entry);

    // Sort list by date descending (i.e. new ones on top)
    let updateVDOMArray = this.state.items.concat(entry).sort((a, b) => {
      let aTimestamp = new Date(a["timestamp"]).getTime();
      let bTimestamp = new Date(b["timestamp"]).getTime();
      return bTimestamp - aTimestamp;
    });
    this.setState({ items: updateVDOMArray });
  };

  showItemDetails(item) {
    let itemId = new Date(item["timestamp"]).getTime().toString();

    localforage
      .getItem(itemId)
      .then(function (value) {
        // This code runs once the value has been loaded
        // from the offline store.
        //console.log(value);
        let desc = value["description"];
        document.getElementById("currentItemDesc").value = desc;

        let itemSummary = `${new Date(item["timestamp"]).toLocaleString()} - ${desc}`;

        document.getElementById("currentItemSummary").innerHTML = itemSummary;

        currentItem = itemId;

        document.getElementById("item-modal").classList.add("is-active");
      })
      .catch(function (err) {
        // This code runs if there were any errors
        console.log(err);
      });
  }

  render() {
    return (
      <div className="App">
        <section className="section">
          <div className="content">
            <h3 className="has-text-centered">Life Log</h3>
          </div>
          <div className="tabs is-centered">
            <ul>
              <li id="enter-data-list-item" className="is-active">
                <a onClick={(e) => showTab("enter-data", e)}>Enter</a>
              </li>
              <li id="list-entries-list-item">
                <a onClick={(e) => showTab("list-entries", e)}>List</a>
              </li>
            </ul>
          </div>
          <div id="enter-data" className="column">
            <form onSubmit={this.handleFormSubmit}>
              <div className="columns is-gapless">
                <div className="column" />
                <div className="column">
                  <div className="field">
                    <label className="label">Log stuff here:</label>
                    <div className="control has-icons-left">
                      <textarea
                        className="textarea"
                        type="text"
                        placeholder="Put anything to describe what life event you want to track"
                        defaultValue=""
                        onChange={this.handleFormChange.bind(
                          this,
                          "description"
                        )}
                      />
                      <span className="icon is-small is-left">
                        <i className="fas fa-asterisk"></i>
                      </span>
                    </div>
                  </div>
                  {/* end: description field */}

                  <div className="field is-grouped">
                    <p className="control">
                      <a
                        type="submit"
                        onClick={this.handleFormSubmit}
                        className="button is-success"
                      >
                        Add
                      </a>
                    </p>
                    <p className="control">
                      <a className="button is-danger">Clear</a>
                    </p>
                  </div>
                </div>
                <div className="column" />
              </div>
              {/* end: enter-data */}
            </form>
          </div>
          <div id="list-entries" className="column" style={{ display: "none" }}>
            <form onSubmit={this.handleSearchButtonSubmit}>
              <div className="field has-addons">
                <div className="control is-expanded">
                  <input
                    className="input"
                    type="text"
                    placeholder="Start typing here to filter"
                    defaultValue=""
                    ref={this.input}
                  />
                </div>
                <div className="control">
                  <a
                    className="button is-info"
                    onClick={this.handleSearchButtonSubmit}
                  >
                    Search
                  </a>
                </div>
              </div>
            </form>
            <table id="entry-items" className="table">
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Description</th>
                </tr>
              </thead>
              <tbody>
                {this.state.items &&
                  this.state.items.map((item) => (
                    <tr
                      key={new Date(item["timestamp"]).getTime()}
                      onClick={(e) => this.handleItemClick(item, e)}
                    >
                      <td>{new Date(item["timestamp"]).toLocaleString()}</td>
                      <td>{item["description"]}</td>
                    </tr>
                  ))}
              </tbody>
            </table>
            <div className="field is-grouped">
              <p className="control">
                <button
                  onClick={downloadCurrentSavedData}
                  className="button is-info"
                >
                  Download Data
                </button>
              </p>
              <p className="control">
                <input
                  type="file"
                  id="importFile"
                  style={{ display: "none" }}
                  onChange={importData}
                />
                <button className="button is-success">
                  <label htmlFor="importFile">Import Data</label>
                </button>
              </p>
            </div>
          </div>
        </section>

        <div id="item-modal" className="modal">
          <div className="modal-background"></div>
          <div className="modal-card">
            <header className="modal-card-head">
              <p className="modal-card-title">Item Details</p>
              <button
                className="delete"
                aria-label="close"
                onClick={closeItemDetailsModal}
              ></button>
            </header>
            <section className="modal-card-body">
              <table className="table">
                <tbody>
                  <tr>
                    <td>Life Event:</td>
                    <td>
                      <textarea
                        id="currentItemDesc"
                        className="textarea"
                        type="text"
                      />
                    </td>
                  </tr>
                </tbody>
              </table>
              <div id="currentItemSummary"></div>
            </section>
            <footer className="modal-card-foot">
              <button className="button is-success">Save</button>
              <button
                onClick={this.handleDeleteItemClick}
                className="button is-danger"
              >
                Delete
              </button>
              <button onClick={shareItem} className="button is-info">
                Share
              </button>
            </footer>
          </div>
        </div>
        {/*<!-- end: item-modal --> */}
      </div>
    );
  }
}

// Sometimes plain old javascript makes everything better
// So put that stuff here

var currentItem = "";

// Set up localforage to use IndexedDB
localforage.config({
  driver: localforage.INDEXEDDB, // Force INDEXEDDB; same as using setDriver()
  name: "life-log",
  version: 1.0,
  size: 4980736, // Size of database, in bytes. WebSQL-only for now.
  storeName: "life_log", // Should be alphanumeric, with underscores.
  description: "life log",
});

function addToLocalDB(itemId, entry) {
  console.log(`adding ${JSON.stringify(entry)}`);

  // insert data into local DB
  localforage
    .setItem(itemId, entry)
    .then(function () {
      return localforage.getItem("key");
    })
    .then(function (value) {
      // we got our value
      showTab("list-entries");
    })
    .catch(function (err) {
      // we got an error
      alert("adding item failed");
    });
}

function removeFromLocalDB(itemId) {
  localforage
    .removeItem(itemId)
    .then(function () {
      // Run this code once the key has been removed.
      console.log(`${JSON.stringify(itemId)} deleted!`);
      closeItemDetailsModal();
    })
    .catch(function (err) {
      // This code runs if there were any errors
      console.log(err);
    });
}

function showTab(tabId) {
  if (tabId === "enter-data") {
    document.getElementById("enter-data").style.display = "block";
    document.getElementById("list-entries").style.display = "none";
    document.getElementById("enter-data-list-item").classList.add("is-active");
    document
      .getElementById("list-entries-list-item")
      .classList.remove("is-active");
  } else if (tabId === "list-entries") {
    document.getElementById("enter-data").style.display = "none";
    document.getElementById("list-entries").style.display = "block";
    document
      .getElementById("enter-data-list-item")
      .classList.remove("is-active");
    document
      .getElementById("list-entries-list-item")
      .classList.add("is-active");
  }
  document.getElementById(tabId).style.display = "block";
}

function closeItemDetailsModal() {
  currentItem = "";
  document.getElementById("item-modal").classList.remove("is-active");
}

function shareItem() {
  console.log("Share button clicked! Let's see if anything else happens.");

  let itemTextToShare =
    document.getElementById("currentItemSummary").innerHTML ||
    "Sorry, your browser and/or phone doesn't allow sharing.";

  if (navigator.share) {
    console.log(
      `Share is allowed. Yay! Will now try to share ${itemTextToShare}`
    );
    navigator
      .share({
        title: "Life Log Item",
        text: itemTextToShare,
      })
      .then(function () {
        console.log("Successful share");
      })
      .catch(function (err) {
        // This code runs if there were any errors
        alert("Error sharing", err);
      });
  } else {
    alert("Sorry, this only works on Android Chrome.");
  }
}

function downloadCurrentSavedData() {
  console.log("Export button clicked! Let's see if anything else happens.");

  let entries = [];

  localforage
    .iterate(function (value, key, iterationNumber) {
      // Resulting key/value pair -- this callback
      // will be executed for every item in the
      // database.
      //console.log([key, JSON.stringify(value)], iterationNumber);
      //add it to the array
      entries.push(value);
    })
    .then(function () {
      console.log("Iteration has completed");
      let strEntries = JSON.stringify(entries);
      console.log(`downloading as json = ${strEntries}`);

      // create download link from indexeddb entries
      let dataStr =
        "data:text/json;charset=utf-8," + encodeURIComponent(strEntries);
      let downloadAnchorNode = document.createElement("a");
      downloadAnchorNode.setAttribute("href", dataStr);
      downloadAnchorNode.setAttribute("download", "life-log-backup.json");

      // download the data to your local
      downloadAnchorNode.click();
      downloadAnchorNode.remove();
    })
    .catch(function (err) {
      // This code runs if there were any errors
      console.log(err);
    });
}

function importData() {
  let selectedFile = document.getElementById("importFile").files[0];
  let fileReader = new FileReader();
  fileReader.readAsText(selectedFile);
  fileReader.onload = function (e) {
    let records = JSON.parse(e.target.result);
    //alert(`records = ${JSON.stringify(records)}`);
    let i = 0;
    const iMax = records.length;
    let alertStr = "";
    for (; i < iMax; i++) {
      let record = records[i];
      let itemId = new Date(record["timestamp"]).getTime().toString();

      // insert data into local DB
      localforage
        .setItem(itemId, record)
        .then(function () {
          return localforage.getItem("key");
        })
        .then(function (value) {
          // we got our value
          console.log(`${JSON.stringify(record)}\n`);
        })
        .catch(function (err) {
          // we got an error
          alert("adding item failed");
        });
      //alertStr += `${JSON.stringify(record)}\n`;
    }
    alert("Importing data done!");
  };
}

export default App;
